.tab h2{font-size:1.2em;margin:0;color:#fff}.tab-bar{display:flex;flex-wrap:wrap;width:100%;list-style:none;padding:0;margin:0}.tab-bar li{padding:.5rem .75rem;margin:0 4px 0 0;font-size:.95rem;border:none;border-radius:5px 5px 0 0;background-color:#1b4f79;font-family:"Lato","Helvetica Neue",Helvetica,Arial,"PingFang TC","黑體-繁","Heiti TC","蘋果儷中黑","Apple LiGothic Medium","微軟正黑體","Microsoft JhengHei",sans-serif;color:#fff;text-decoration:none;cursor:pointer;user-select:none;touch-action:none;width:80px;height:1rem;text-align:center;overflow:hidden}#addLayer{width:initial}.tab-bar li:hover{background-color:#7659f5}.tab-bar li.active{background-color:#29a37e}.tab-bar li.dragging{border-radius:5px;background-color:#546862;box-shadow:5px 5px rgba(128,128,128,.25);cursor:grab;z-index:100}.tab{display:flex;flex-wrap:wrap;align-items:center;justify-content:space-around;padding:20px 0px;background-color:#29a37e;display:none}.tab.active{display:flex}.tab label{color:#fff}.tab img{max-height:250px;margin-left:auto;margin-right:auto;display:block}.tab>div{margin:0 1em}.layer-panel__img-container{max-width:250px;max-height:250px}@media(max-width: 768px){.tab-bar li{box-sizing:border-box;padding:7px 16px;margin:0 2px 0 0;font-size:14px;width:calc(33.3333333333% - 4px);height:32px}}